import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion, useScroll, useTransform } from 'framer-motion';
import PaymentModal from '../components/PaymentModal';
import './LandingPage.css';

const LandingPage = () => {
  const navigate = useNavigate();
  const [paymentModal, setPaymentModal] = useState({ isOpen: false, planName: '', planPrice: '' });
  const { scrollYProgress } = useScroll();

  const y = useTransform(scrollYProgress, [0, 1], ['0%', '50%']);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        duration: 0.5,
        staggerChildren: 0.1
      }
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.8,
        ease: "easeOut"
      }
    }
  };

  const buttonVariants = {
    hover: { scale: 1.05, boxShadow: "0 10px 30px rgba(59, 130, 246, 0.3)" },
    tap: { scale: 0.95 }
  };

  const handleGetStarted = () => {
    navigate('/home');
  };

  const handleFreeTrial = () => {
    navigate('/login');
  };

  const handlePlanPurchase = (planName, planPrice) => {
    setPaymentModal({ isOpen: true, planName, planPrice });
  };

  const closePaymentModal = () => {
    setPaymentModal({ isOpen: false, planName: '', planPrice: '' });
  };

  return (
    <motion.div
      className="container"
      variants={containerVariants}
      initial="hidden"
      animate="visible"
    >
      {/* Navigation */}
      <motion.nav
        className="navbar"
        initial={{ y: -100 }}
        animate={{ y: 0 }}
        transition={{ duration: 0.6 }}
      >
        <div className="nav-content">
          <motion.h2
            className="logo"
            whileHover={{ scale: 1.05 }}
          >
            NutriTrack
          </motion.h2>
          <motion.button
            className="loginButton"
            onClick={handleFreeTrial}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            Login
          </motion.button>
        </div>
      </motion.nav>

      {/* Hero Section */}
      <motion.section
        className="hero"
        variants={itemVariants}
        style={{ y }}
      >
        <div className="heroContent">
          <motion.h1
            className="heroTitle"
            variants={itemVariants}
          >
            Transform Your
            <span className="highlight"> Health Journey</span>
          </motion.h1>
          <motion.p
            className="heroSubtitle"
            variants={itemVariants}
          >
            AI-powered nutrition tracking that adapts to your lifestyle, goals, and preferences.
            Start your journey to better health today.
          </motion.p>
          <motion.div
            className="heroButtons"
            variants={itemVariants}
          >
            <motion.button
              className="ctaButton primary"
              onClick={handleGetStarted}
              variants={buttonVariants}
              whileHover="hover"
              whileTap="tap"
            >
              Start Free Trial
            </motion.button>
            <motion.button
              className="ctaButton secondary"
              onClick={handleFreeTrial}
              variants={buttonVariants}
              whileHover="hover"
              whileTap="tap"
            >
              Watch Demo
            </motion.button>
          </motion.div>
        </div>
        <motion.div
          className="heroVisual"
          variants={itemVariants}
        >
          <div className="phoneMockup">
            <div className="screen"></div>
          </div>
        </motion.div>
      </motion.section>

      {/* Key Value Propositions */}
      <motion.section
        className="features"
        variants={itemVariants}
      >
        <motion.h2
          className="sectionTitle"
          variants={itemVariants}
        >
          Why Choose NutriTrack
        </motion.h2>
        <motion.div
          className="featuresGrid"
          variants={containerVariants}
        >

          <motion.div
            className="featureItem"
            variants={itemVariants}
            whileHover={{ scale: 1.05 }}
          >
            <h3>Real-time progress tracking</h3>
            <p>Monitor your nutrition intake, weight changes, and health metrics in real-time.</p>
          </motion.div>
          <motion.div
            className="featureItem"
            variants={itemVariants}
            whileHover={{ scale: 1.05 }}
          >
            <h3>Expert-curated meal plans</h3>
            <p>Plans designed by nutritionists and dietitians for optimal health outcomes.</p>
          </motion.div>
          <motion.div
            className="featureItem"
            variants={itemVariants}
            whileHover={{ scale: 1.05 }}
          >
            <h3>Community support system</h3>
            <p>Join thousands of users sharing recipes, tips, and celebrating successes together.</p>
          </motion.div>
        </motion.div>
      </motion.section>

      {/* Social Proof Section */}
      <motion.section
        className="socialProof"
        variants={itemVariants}
      >
        <motion.h2
          className="sectionTitle"
          variants={itemVariants}
        >
          Trusted by Thousands
        </motion.h2>
        <motion.div
          className="socialProofGrid"
          variants={containerVariants}
        >
          <motion.div
            className="proofItem"
            variants={itemVariants}
            whileHover={{ scale: 1.05 }}
          >
            <div className="statNumber">50,000+</div>
            <p>Active Users</p>
          </motion.div>
          <motion.div
            className="proofItem"
            variants={itemVariants}
            whileHover={{ scale: 1.05 }}
          >
            <div className="statNumber">4.8/5</div>
            <p>App Store Rating</p>
          </motion.div>
          <motion.div
            className="proofItem"
            variants={itemVariants}
            whileHover={{ scale: 1.05 }}
          >
            <div className="statNumber">95%</div>
            <p>User Satisfaction</p>
          </motion.div>
          <motion.div
            className="proofItem"
            variants={itemVariants}
            whileHover={{ scale: 1.05 }}
          >
            <div className="trustBadge">Backed by Science</div>
            <p>Evidence-based nutrition</p>
          </motion.div>
        </motion.div>
      </motion.section>



      {/* Pricing Plans */}
      <motion.section
        className="pricing"
        variants={itemVariants}
      >
        <motion.h2
          className="sectionTitle"
          variants={itemVariants}
        >
          Choose Your Plan
        </motion.h2>
        <motion.div
          className="pricingGrid"
          variants={containerVariants}
        >
          <motion.div
            className="pricingCard"
            variants={itemVariants}
            whileHover={{ scale: 1.05, y: -10 }}
          >
            <h3>Basic Plan</h3>
            <div className="price">799/month</div>
            <ul>
              <li>Basic meal tracking</li>
              <li>Daily nutrition summary</li>
              <li>Access to community recipes</li>
              <li>Email support</li>
            </ul>
            <motion.button
              className="pricingButton"
              onClick={() => handlePlanPurchase('Basic Plan', '799/month')}
              variants={buttonVariants}
              whileHover="hover"
              whileTap="tap"
            >
              Get Started
            </motion.button>
          </motion.div>
          <motion.div
            className="pricingCard pro"
            variants={itemVariants}
            whileHover={{ scale: 1.05, y: -10 }}
          >
            <h3>Pro Plan</h3>
            <div className="price">1199/month</div>
            <ul>
              <li>All Basic features</li>
              <li>Custom meal planning</li>
              <li>Progress tracking with charts</li>
              <li>Recipe customization</li>
              <li>Chat support</li>
            </ul>
            <motion.button
              className="pricingButton pro"
              onClick={() => handlePlanPurchase('Pro Plan', '1199/month')}
              variants={buttonVariants}
              whileHover="hover"
              whileTap="tap"
            >
              Go Pro
            </motion.button>
          </motion.div>
          <motion.div
            className="pricingCard premium"
            variants={itemVariants}
            whileHover={{ scale: 1.05, y: -10 }}
          >
            <h3>Premium Plan</h3>
            <div className="price">1599/month</div>
            <ul>
              <li>All Pro features</li>
              <li>AI-powered meal recommendations</li>
              <li>Advanced analytics and reports</li>
              <li>Unlimited meal plans</li>
              <li>Priority support</li>
            </ul>
            <motion.button
              className="pricingButton premium"
              onClick={() => handlePlanPurchase('Premium Plan', '1599/month')}
              variants={buttonVariants}
              whileHover="hover"
              whileTap="tap"
            >
              Go Premium
            </motion.button>
          </motion.div>
        </motion.div>
      </motion.section>

      {/* Success Stories */}
      <motion.section
        className="testimonials"
        variants={itemVariants}
      >
        <motion.h2
          className="sectionTitle"
          variants={itemVariants}
        >
          Success Stories from Our Community
        </motion.h2>
        <motion.div
          className="testimonialsGrid"
          variants={containerVariants}
        >
          <motion.div
            className="testimonialCard"
            variants={itemVariants}
            whileHover={{ scale: 1.05, y: -10 }}
          >
            <p>"Lost 25 pounds in 4 months with NutriTrack's AI recommendations. The meal timing guidance was a game-changer!"</p>
            <cite>- Jennifer L., Weight Loss Journey</cite>
          </motion.div>
          <motion.div
            className="testimonialCard"
            variants={itemVariants}
            whileHover={{ scale: 1.05, y: -10 }}
          >
            <p>"Gained 12 pounds of muscle while improving my overall health. The macronutrient tracking is spot on."</p>
            <cite>- Alex M., Fitness Enthusiast</cite>
          </motion.div>
          <motion.div
            className="testimonialCard"
            variants={itemVariants}
            whileHover={{ scale: 1.05, y: -10 }}
          >
            <p>"Managing PCOS became so much easier with the condition-specific meal plans. Highly recommend!"</p>
            <cite>- Priya S., Health Warrior</cite>
          </motion.div>
        </motion.div>
      </motion.section>

      <PaymentModal
        isOpen={paymentModal.isOpen}
        onClose={closePaymentModal}
        planName={paymentModal.planName}
        planPrice={paymentModal.planPrice}
      />

    </motion.div>
  );
};

export default LandingPage;
